import request from "../utils/request";

class Store {
  /**
   * localStorage
   */
  public setStorage = (key: string, value: string) => {
    window.localStorage.setItem(key, value);
  };

  /**
   * localStorage
   */
  public getStorage = (key: string) => {
    return window.localStorage.getItem(key) || undefined;
  };

  //banner
  public getBanner = (type: number) => {
    const url = "/banner";
    const params = {
      type
    };
    return request({ url, params });
  };

  //
  public getPersonalized = (limit: number) => {
    const url = "/personalized";
    const pre = {
      limit
    };
    return request({ url, pre });
  };

  //
  public getTopListDetail = () => {
    const url = "/toplist/detail";
    return request({ url });
  };

  //
  public getHotPlay = () => {
    const url = "/playlist/hot";
    return request({ url });
  };

  /*  */
  public getHotPlayDetail = (tag: string) => {
    const url = "/top/playlist";
    const params = {
      cat: tag
    };
    return request({ url, params });
  };

  /**
   * 
   */

  public getTopSinger = (offset = 0, limit = 50) => {
    const url = "/top/artists";
    const params = {
      offset,
      limit
    };
    return request({ url, params });
  };

  /**
   * 
   */
  public getCategorySinger = (
    cat: number,
    initial?: string,
    offset?: number,
    limit?: number
  ) => {
    const url = "/artist/list";
    const params = {
      cat,
      initial,
      offset,
      limit
    };
    return request({ url, params });
  };

  /**
   * 
   */
  public submitLoginByEmail = (email: string, password: string) => {
    const url = "/login";
    const params = {
      email,
      password
    };
    return request({ url, params });
  };

  /**
   *
   */
  public getPlayDetail = (id: string) => {
    const url = "playlist/detail";
    const params = {
      id
    };
    return request({ url, params });
  };

  /**
   *
   */
  public getSingerPlayDetail = (id: string) => {
    const url = "artists";
    const params = {
      id
    };
    return request({ url, params });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   * @author: FBB
   */
  public getUserDetail = (uid: string) => {
    const url = "/user/detail";
    const params = {
      uid
    };
    return request({ url, params });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  public getRecommendSongs = () => {
    const url = "/recommend/songs";
    return request({ url });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   * @author: FBB
   */
  public getRecommendResource = () => {
    const url = "/recommend/resource";
    return request({ url });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   * @author: FBB
   */
  public comfirmLoginStatus = () => {
    const url = "/login/status";
    return request({ url });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   * @author: FBB
   */
  public getSearchDefault = () => {
    const url = "/search/default";
    return request({ url });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   * @author: FBB
   */
  public getSearchHotDetail = () => {
    const url = "/search/hot/detail";
    return request({ url });
  };

  /**
   * @description:
   * @param {type}
   * @return:
   */
  public getSearchKeywords = (keywords: string) => {
    const url = "/search";
    const params = {
      keywords
    };
    return request({ url, params });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  public loginOut = () => {
    const url = "/logout";
    return request({ url });
  };

  /**
   * @description: 
   * @param {type}
   * @return:
   */
  public refreshLogin = () => {
    const url = "/login/refresh";
    return request({ url });
  };

  /**
   * @description:  , mv, dj 
   * @param {type}
   * @return:
   */
  public getUserSubcount = () => {
    const url = "/user/subcount";
    return request({ url });
  };

  /**
   * @description: 
   * @param {id}
   * @return:
   * @author: FBB
   */
  public getSongDetail = (id: string) => {
    const url = "/song/detail";
    const params = {
      ids: id
    };
    return request({ url, params });
  };

  /**
   * @description: url
   * @param {id}
   * @return:
   * @author: FBB
   */
  public getSongUrl = (id: string) => {
    const url = "/song/url";
    const params = {
      id
    };
    return request({ url, params });
  };
}

export const store = new Store();
